.App {
  max-width: 1000px;
  margin: auto;
  position: relative;
}

.App img,
.App svg {
  left: 0;
  position: absolute;
  width: 100%;
}

.wingColor, .wingColor1, .wingColor2, .wingColor3 {
  fill: #f0f0f0;
  stroke-width: .5rem;
  cursor: pointer;
}

.wingColor {
stroke: #616161;
}

.wingColor.is_active {
  fill: #616161;
}
.wingColor1 {
  stroke: #2a1310;
}

.wingColor1.is_active {
  fill: #2a1310;
}

.wingColor2 {
  stroke: #9a7398;
}

.wingColor2.is_active {
  fill: #9a7398;
}

.wingColor3 {
  stroke: #6a73fd;
}

.wingColor3.is_active {
  fill: #6a73fd;
}


.greenery {
  fill: rgba(0, 255, 0, 0.7);
  stroke: darkgreen;
  stroke-width: 1px;
}
