/*-- //Responsive Design --*/


.pilled-selection {
	border:none!important;
	display: flex;
	justify-content: center;
}
.pilled-selection .nav-item {
	border: none;
	color: black;
	border-radius: 5px;
	margin:5px;
	width: 120px;
	text-align:center;
}
.pilled-selection .nav-item:hover {
	color: #2dde98;
}
.pilled-selection .nav-item.active {
	background-color: #2dde98;
	color: white;
}