.App {
  max-width:1000px;
  margin:auto;
  padding-top:20px;
  padding-bottom: 20px;
  position: relative;
}
.App img, .App svg{
  left:0;
  position:absolute;
  width: 100%;
}
.App img.placeholder {
  position: static;
}
.otherside {
  fill: #f8f8f8;
  stroke: #616161;
  cursor: pointer;
}
.otherside2 {
  fill: #f8f8f8;
  stroke: #2a7398;
  cursor: pointer;
}

.otherside.is_active {
  fill: #616161;
}

.otherside2.is_active {
  fill: #2a7398;
}
.greenery {
  fill: rgba(0, 255, 0, 0.7);
  stroke: darkgreen;
  stroke-width: 1px;
}

.step-progress {
  text-align: center;
  /* padding-top:150px; */
}
.step-progress:before {
  display:block;
  content: ' ';
  clear:both;
  padding-top: 35px;
}
.step-progress .multi-step {
  margin-bottom: 35px;
}

.App img,
.App svg {
  position: absolute;
  width: 100%;
}
img {
  z-index: -1;
}
svg {
  z-index: 1;
  margin-left: 0.35%;
}
.reserveMap svg {
  margin-left: 0;
}
path {
  opacity: 1;
  transition: opacity linear 0.2s 0.1s;
  stroke-width: 1px;
  fill: #871539;
  stroke: #a68c66;
}
path.b1 {
  fill: #ffff00;
}
path.b2 {
  fill: #ffc000;
}
path.b3 {
  fill: #ffc000;
}
path.b4 {
  fill: #ffc000;
}
path[data-price="-1"] {
  fill: #a68c66;
  stroke: #871539;
}
path[data-price="0"] {
  fill: #777;
  stroke: #871539;
}
path.greenery {
  fill: green;
  stroke: darkgreen;
}
path:hover {
  fill: rgba(113, 21, 57, 0.7);
  stroke: #a68c66;
  stroke-width: 1px;
}
path.b1:hover {
  fill: rgba(255, 255, 0, 0.7);
}
path.b2:hover {
  fill: rgba(255, 192, 0, 0.7);
}
path.b3:hover {
  fill: rgba(255, 192, 0, 0.7);
}
path.b4:hover {
  fill: rgba(255, 192, 0, 0.7);
}
path[data-price="-1"]:hover {
  fill: rgba(166, 140, 102, 0.7);
  stroke: #871539;
  stroke-width: 1px;
}
path[data-price="0"]:hover {
  fill: rgba(7, 7, 7, 0.7);
  stroke: #871539;
  stroke-width: 1px;
}
path.greenery:hover {
  fill: rgba(0, 255, 0, 0.7);
  stroke: darkgreen;
  stroke-width: 1px;
}
.popover {
  padding: 0;
  overflow: hidden;
}
.popover {
  border-color: #a68c66;
}
.popover.spec {
  border-color: #871539;
}
.popover.sold {
  border-color: #871539;
}
.popover.greenery {
  border-color: darkgreen;
}
.popover .arrow {
  display: none;
}
.popover .popover-footer,
.popover .popover-title {
  text-align: center;
  margin: 0;
  padding: 8px 14px;
  font-size: 14px;
  border: none;
  color: #f7f7f7;
  background-color: #871539;
}
.popover.b1 .popover-footer,
.popover.b1 .popover-title {
  color: #f7f7f7;
  background-color: #ffff00;
}
.popover.b2 .popover-footer,
.popover.b2 .popover-title {
  color: #f7f7f7;
  background-color: #ffc000;
}
.popover.b3 .popover-footer,
.popover.b3 .popover-title {
  color: #f7f7f7;
  background-color: #ffc000;
}
.popover.b4 .popover-footer,
.popover.b4 .popover-title {
  color: #f7f7f7;
  background-color: #ffc000;
}
.popover.spec .popover-footer,
.popover.spec .popover-title {
  color: #f7f7f7;
  background-color: #a68c66;
}
.popover.sold .popover-footer,
.popover.sold .popover-title {
  color: #f7f7f7;
  background-color: #777;
}
.popover.greenery .popover-footer,
.popover.greenery .popover-title {
  color: #f7f7f7;
  background-color: green;
}
.popover.greenery .popover-footer {
  display: none;
}
.popover.greenery .block {
  display: none;
}
.address {
  font-weight: 700;
  color: #871539;
}
.b1 .address {
  color: #ffff00;
}
.b2 .address {
  color: #ffc000;
}
.b3 .address {
  color: #ffc000;
}
.b4 .address {
  color: #ffc000;
}
.spec .address {
  color: #a68c66;
}
.sold .address {
  color: #777;
}
.greenery .address {
  color: green;
}
hr {
  margin: 10px auto;
  opacity: 0.4;
  border-color: #871539;
}
.spec hr {
  border-color: #a68c66;
}
.sold hr {
  border-color: #a68c66;
}
.greenery hr {
  border-color: green;
}
dt,
dd {
  display: inline-block;
}
dt {
  color: #616161;
  font-weight: 700;
  width: 50px;
  margin-left: 10px;
  vertical-align: top;
}
dd {
  min-width: 50px;
  margin-left: 15px;
  text-align: right;
  color: #871539;
}
dt:after {
  content: " :";
  float: right;
}
.spec dd {
  color: #a68c66;
}

.sold dd {
  color: #a68c66;
}
.top_border {
  border-top: 1px solid rgba(49, 49, 49, 0.5);
  margin-top: 5px;
  padding-top: 3px;
}
.inline-block {
  display: inline-block;
}
.ridgeProperty .extras {
  display: none;
}
.static-label {
  display: none;
}
.greenery .static-label {
  display: block;
  text-align: center;
  font-weight: bold;
  color: darkgreen;
}
.otherside {
  fill: #f8f8f8;
  stroke: #616161;
  cursor: pointer;
}
path.otherside.is_active {
  fill: rgba(220, 220, 220, 1);
  stroke: #a68c66;
  stroke-width: 1px;
}
